.yearDisplay {
    margin-bottom: 2rem;
    border: 1px solid white;
    border-radius: 0.5rem;
    overflow: hidden;
    width: 20%;
    margin-top: 1rem;
}

@media (max-width: 800px) {
    .yearDisplay {
        width: 100%;
    }
}

.yearDisplay > .wrapper {
    padding: 1rem;
}
